html, body, #root, canvas {
  width: 100vw;
  height: 100vh;
  margin: 0;
}
.overlay {
	display:none;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: hsla(0, 0%, 0%, 0.3);
  z-index: 1
    
  }
  .mybutton {
    border: none;
    position: absolute;
    font-family: Arial;
    font-weight: 700;
    display: flex;
    transform: scale(0.7);
    background-image: linear-gradient(to bottom right, #cccccc 0%, #d5d5d5 20%, black 50%, black 70%, #d7cbcb 100%);
    border-radius: 100px;
    width: 100px;
    height: 100px;
    margin: auto;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.7);
    display: grid;
    cursor: pointer;
    bottom: 2px;
    left: 50%;
    margin-left: auto;
    text-align: center;
    vertical-align: middle;
    -moz-animation: shakeme 2s infinite;
    -o-animation: shakeme 2s infinite;    
    -webkit-animation: shakeme 2s infinite;
    animation: shakeme 2s infinite;
}

@keyframes shakeme {
  0% {
    transform : scale(0.7);
  }
  5% {
    transform :scale(0.5);
  }
  10% {
    transform :scale(0.9);
  }
  15% {
    transform :scale(0.8);
  }
  20% {
    transform :scale(0.9);
  }
  30% {
    transform :scale(1);
  }
}
.mybutton .mybuttoninner {
  width: 85px;
  height: 85px;
  border-radius: 100px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#9e9e9e), color-stop(40%, white), color-stop(70%, #383838), to(white));
  background-image: linear-gradient(to bottom, #9e9e9e 0%, white 40%, #383838 70%, white 100%);
  margin: 2px auto 6px auto;
  padding: 2px;
}

.mybutton .mybuttoninner .mybuttoninner2 {
  width: 85px;
  height: 85px;
  background-color: #6b6b6b;
  border-radius: 100px;
  -webkit-box-shadow: inset 0 10px 30px rgba(0, 0, 0, 0.7);
          box-shadow: inset 0 10px 30px rgba(0, 0, 0, 0.7);
  display: -ms-grid;
  display: grid;
}

.mybutton .mybuttoninner .mybuttoninner2 ul {
  padding: 0;
}

.mybutton .mybuttoninner .mybuttoninner2 ul li {
  color: white;
  list-style-type: none;
  text-align: center;
  text-transform: uppercase;
  padding: 7px;
}

.mybutton .mybuttoninner .mybuttoninner2 ul .mybuttoninnerline2 {
  margin: auto;
  background-color: white;
  width: 50px;
  height: 2px;
}

.mybutton .mybuttoninner .mybuttoninner2 ul .mybuttoninnerline {
  margin: auto;
  width: 50px;
  height: 2px;
  margin: auto;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

@-webkit-keyframes animshadow {
  50% {
    -webkit-box-shadow: 0 0px 20px white;
            box-shadow: 0 0px 20px white;
    background-color: white;
  }
  100% {
    -webkit-box-shadow: 0 0px 4px #e00000;
            box-shadow: 0 0px 4px #e00000;
    background-color: #e00000;
  }
}

@keyframes animshadow {
  50% {
    -webkit-box-shadow: 0 0px 20px white;
            box-shadow: 0 0px 20px white;
    background-color: white;
  }
  100% {
    -webkit-box-shadow: 0 0px 4px #e00000;
            box-shadow: 0 0px 4px #e00000;
    background-color: #e00000;
  }
}

@-webkit-keyframes pressedbutton {
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-box-shadow: none;
            box-shadow: none;
  }
}

@keyframes pressedbutton {
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-box-shadow: none;
            box-shadow: none;
  }
}

.mybutton:hover .mybuttoninnerline {
  -webkit-box-shadow: 0 0px 4px #e00000;
          box-shadow: 0 0px 4px #e00000;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  
  background-color: #e00000;
}

.mybutton:focus {
  border: none;
  outline: none;
  -webkit-animation: pressedbutton 0.2s alternate;
          animation: pressedbutton 0.2s alternate;
}

.mybutton:focus .mybuttoninner {
  border: none;
}

.mybutton:focus .mybuttoninner2 {
  border: none;
}

.mybutton:focus .mybuttoninnerline2 {
  border: none;
}

.mybutton:focus .mybuttoninnerline {
  -webkit-animation: animshadow 0.4s forwards;
          animation: animshadow 0.4s forwards;
  border: none;
}
